import gql from 'graphql-tag';

export const UpdateSecretClientKeyMutation = gql`
  mutation updateSecretClientKey($secretClientKey: String) {
    updateSecretClientKey(secretClientKey: $secretClientKey) {
      secretClientKey
    }
  }
`;

const JobFragment = gql`
  fragment Job on Job {
    id
    status
    request {
      type
      callbackURL
      parameters {
        idPhoto
        userPhoto
        twicPhoto
        carInsurancePhoto
        dotPhoto
        firstName
        lastName
        dob
      }
    }
    result {
      success
      type
      country
      state
      confidences {
        id
        backId
        selfie
        idMatch
        faceMatch
      }
    }
    errors {
      type
      message
      suggestion
    }
    submitted
  }
`;

export const SubmitJobMutation = gql`
  mutation submitJob($type: String!, $callbackURL: String, $params: JobParams) {
    submitJob(type: $type, callbackURL: $callbackURL, params: $params) {
      ...Job
    }
  }
  ${JobFragment}
`;

export const JobsQuery = gql`
  query jobs(
    $id: ID
    $ids: [ID]
    $type: String
    $status: String
    $token: String
    $to: String
    $from: String
    $withPhotos: Boolean
    $sortOrder: String
    $sortBy: String
    $page: Int
    $pageSize: Int
  ) {
    jobs(
      withPhotos: $withPhotos
      id: $id
      ids: $ids
      status: $status
      type: $type
      to: $to
      token: $token
      from: $from
      sortOrder: $sortOrder
      sortBy: $sortBy
      page: $page
      pageSize: $pageSize
    ) {
      total
      totalPages
      pageSize
      page
      items {
        ...Job
      }
    }
  }
  ${JobFragment}
`;
